export default {
  methods: {
    /**
     * 
     * @param {Object} option 
     * @param {String} target 
     */
    MyDraw(option, target) {
      let myChart = this.$echarts.init(document.getElementById(target));
      window.addEventListener("resize", () => {
        myChart.resize();
      });
      myChart.setOption(option);
      myChart.hideLoading();
    },
    /**
     * 
     * @param {Array} target 
     */
    showLoading(target) {
      if (!Array.isArray(target)) return console.error('showLoading target必须为数组!')
      for (let i = 0; i < target.length; i++) {
        let myChart = this.$echarts.init(document.querySelector(target[i]));
        myChart.showLoading()
        if (myChart.getWidth() === 0) {
          myChart.resize();
        }
      }
    }
  }
}