<template>
  <div class="data container-xl">
    <div class="row">
      <div class="col-6 draw-item" id="category"></div>
      <div class="col-6 draw-item" id="user-add"></div>
      <div class="col-6 draw-item" id="user-add"></div>
      <div class="col-6 draw-item">1</div>
      <div class="col-6 draw-item">1</div>
    </div>
  </div>
</template>

<script>
import draw from "../utils-admin/mixindraw";
export default {
  mixins: [draw],
  activated() {
    // this.$store.dispatch("getStatistics");
    this.getCategoryData();
  },
  methods: {
    async getCategoryData() {
      //category
      this.showLoading(["#category"]);
      let data = this.$store.state.blog.drawCategory;
      if (!data.length) {
        data = await this.$store.dispatch("getTypes_num");
      }
      let category = data.map(v => v.name);
      let category_item_num = data.map(v => v.num);
      let category_item_like = data.map(v => v.likes_num);
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["博客数量", "收藏数量"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          data: category
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "博客数量",
            type: "bar",
            stack: "总量",
            label: {
              show: true
            },
            data: category_item_num
          },
          {
            name: "收藏数量",
            type: "bar",
            stack: "总量",
            label: {
              show: true
            },
            data: category_item_like
          }
        ]
      };
      this.MyDraw(option, "category");
    }
  }
};
</script>

<style lang="scss" scoped>
.draw-item {
  min-height: 250px;
  min-width: 250px;
}
</style>